.promo-face {
    background: url("../img/photos/eyal-profile.jpg") no-repeat left center;
    background-size: contain;
    text-align: center;

    &__fade {
        padding-top: 76px;
        padding-bottom: 88px;
    }

    &__title {
        margin: 0 auto 10px;
        width: 559px;
        font-size: 44px;
        font-weight: bold;
        line-height: 1.18;
        text-align: center;
        color: $dark-grey;
    }

    &__desc {
        margin: 0 auto 30px;
        width: 673px;
        font-size: 18px;
        text-align: center;
        color: $dark-grey;
    }

    &__btn {
        position: relative;
        display: inline-block;
        box-sizing: border-box;
        padding: 18px 48px 18px 38px;
        border-radius: 100px;
        background-color: $sunflower-yellow;
        box-shadow: 0px 0px 0px 8px rgba(255, 217, 1, 0.14);
        border: none;

        font-size: 18px;
        font-weight: 600;
        text-align: left;
        color: $dark-grey;
        @include trans(0.3s);

        &::after {
            content: '→';
            position: absolute;
            top: 17px;
            right: 20px;
            @include trans(0.3s);
        }
      
        &:hover {
            cursor: pointer;
            transform: scale(1.03);
	        box-shadow: rgba(115, 94, 0, 0.24) 10px 16px 40px 0;
      
            &::after {
                right: 16px;
            }
          }
    }
}

@media(max-width: $lg) {
    .promo-face {
        &__fade {
            width: 673px;
            margin-left: calc(50% - 200px);
        }
    }
}

@media(max-width: $md) {
    .promo-face {

        &__fade {
            padding-top: 76px;
            padding-bottom: 88px;
            width: 60%;
            margin-right: 4%;
            margin-left: auto;
        }

        &__title {
            width: auto;
            font-size: 34px;
            line-height: 1.24;
        }

        &__desc {
            width: auto;
        }

    }
}

@media(max-width: $sm) {
    .promo-face {
        background-size: auto 100%;

        &__title {
            width: 83%;
            font-size: 22px;
        }

        &__desc {
            margin-bottom: 15px;
            width: 88%;
            font-size: 18px;
            line-height: 1.46;
        }

        &__btn {
            padding: 18px 42px 18px 32px;
        }

        &__fade {
            width: 100%;
            padding-top: 34px;
            padding-bottom: 26px;
            background-color: rgba($white, 0.7);
        }
    }
}